@import url(https://fonts.googleapis.com/css?family=Coming+Soon);
body {
  margin: 0;
  padding: 0;
  font-family: "Coming Soon", cursive;
}

.App {
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  background: url("http://cdn.desktopwallpapers4.me/wallpapers/abstract/2560x1600/1/3603-white-crumpled-paper-2560x1600-abstract-wallpaper.jpg");
  background-size: 100% 100%;
  background-position: center center;
}

.Hangman {
  color: black;
  text-align: center;
  zoom: 1.3;
}

.Hangman img {
  border: 10px solid rgb(255, 255, 255);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.27),
    0 0 40px rgba(0, 0, 0, 0.06) inset;
}

.Hangman h1 {
  font-size: 4rem;
  font-weight: 100;
  margin-bottom: 0.1em;
  margin-top: 0;
}

.Hangman-word {
  letter-spacing: 1em;
  margin: 0.4em -1em 0.2em 0;
  font-size: 2rem;
}

.Hangman-btns {
  text-align: center;
  display: inline-block;
  width: 250px;
  margin-top: 0;
}

.Hangman button {
  border: none;
  font-family: inherit;
  font-size: 1rem;
  cursor: pointer;
  width: 30px;
  margin: 5px 2px 2px 2px;
  letter-spacing: 1px;
  outline: none;
  background: #ffc107;
  color: #fff;
  top: 0px;
  box-shadow: 0 3px #ff9800;
  border-radius: 5px;
  padding-top: 4px;
  position: relative;
}

.Hangman button:hover {
  background-color: #fca82c;
  box-shadow: 0 2px #ff9800;
  top: 1px; 
}

.Hangman button:active{
  box-shadow: 0 0 #ff9800;
  top: 3px;  
}

.Hangman button:disabled {
  background: #bdbdbd;
  color: #eeeeee;
  box-shadow: 0 3px #9e9e9e;
}

#restartBtn{
  width: auto;
  margin-bottom: 1rem;
  position: relative;
  background-color: #5aacc8;
  box-shadow: 0 3px #3e849b;
  top: 0px;
}

#restartBtn:hover{
  top: 1px;
  box-shadow: 0 2px #3e849b;
}

#restartBtn:active{
  top: 3px;
  box-shadow: 0 0 #3e849b;
}

